import React, { Component } from 'react';
import './Header.css';
import { Link } from 'react-router-dom';

export default class Header extends Component {
  constructor() {
    super()
    this.state = {
      isActive: false
    }
  }
  toggleActive() {
    this.setState({
      isActive: !this.state.isActive
    })
  }
  render() {
    const {isActive} = this.state;
    return (
      <header>
        <div className="insta">
          <a href="https://www.instagram.com/explore/tags/hotdogs">
            <img src="img/Instagram.png" alt="instagram"></img>
            <p>#hotdogs</p>
          </a>
        </div>
        <div className="hots-grid-wrapper">
          <div className="imgs-grid-1">
            <div className="shadow"></div>
            <img src="img/smallhot1.png" alt="hotdog"></img>
            <img src="img/smallhot2.png" alt="hotdog"></img>
            <img src="img/smallhot3.png" alt="hotdog"></img>
            <img src="img/smallhot4.png" alt="hotdog"></img>
            <img src="img/smallhot5.png" alt="hotdog"></img>
            <img src="img/smallhot6.png" alt="hotdog"></img>
          </div>
          <div className="imgs-grid-2">
            <img src="img/smallhot1.png" alt="hotdog"></img>
            <img src="img/smallhot2.png" alt="hotdog"></img>
            <img src="img/smallhot3.png" alt="hotdog"></img>
            <img src="img/smallhot4.png" alt="hotdog"></img>
            <img src="img/smallhot5.png" alt="hotdog"></img>
            <img src="img/smallhot6.png" alt="hotdog"></img>
          </div>
        </div>
        <nav>
          <ul className={`menu ${isActive ? 'active' : '' }`}>
            <Link to="/">
              <li>Menu</li>
            </Link>
              <li>Catering</li>
              <li>About us</li>
            <Link to="/Contact">
              <li>Contact</li>
            </Link>
          </ul>
          <img id="btn-menu" src="img/menu.png" alt="menu" onClick={()=>this.toggleActive()}></img>
        </nav>
      </header>
    );
  }
}


