import React, { Component } from 'react';
import axios from 'axios';
import './Contact.css';

export default class Contact extends Component {
  constructor() {
    super()
    this.state = {
      name: '',
      email: '',
      comment: '',
    }
  }
  changeHandler = e => {
    this.setState({[e.target.name]: e.target.value});
  }
  submitHandler = e => {
    e.preventDefault();
    axios.post(' https://formula-test-api.herokuapp.com/contact', this.state).then(res => {
      console.log(res.data);
    });
    e.target.name.value = '';
    e.target.email.value = '';
    e.target.comment.value = '';
    this.state.name = ''
    this.state.email = ''
    this.state.comment = ''
  }
  render() {
    const { name, email, comment } = this.state
    return (
      <div>
        <form onSubmit={this.submitHandler}>
          <input type="text" name="name" placeholder="Name" value={name} onChange={this.changeHandler}></input>
          <input type="email" name="email" placeholder="Email" value={email} onChange={this.changeHandler}></input>
          <textarea name="comment" rows="10" cols="30" placeholder="Comment" value={comment} onChange={this.changeHandler}></textarea>
          <input type="submit" className="sub"></input>
        </form>
      </div>
    );
  }
}
