import React, { Component } from 'react';
import axios from 'axios';
import './Home.css';

export default class Home extends Component {
  state = {
    menuItems: [],
  };
  componentDidMount() {
    axios.get('https://formula-test-api.herokuapp.com/menu').then(res => {
      const items = res.data;
      // const today = new Date('01/01/2000'); 
      const today = new Date();
      console.log(items);
      const filteredArr = items.filter((el) => {
        const elSplit = el.expirationDate.split('-');
        const itemDate = new Date(elSplit[2], +elSplit[0] - 1, elSplit[1]);
        return itemDate > today;
      });
      console.log(filteredArr);
      this.setState({ menuItems: filteredArr });
    });
  }
  render() {
    return (
      <div>
        <section className="intro">
          <img src="img/hotdog.png" alt="hotdog"></img>
          <h1>Dirty Dogs serves all-beef, vegan and vegatagian hot dogs.</h1>
          <div className="btn-intro-wrapper">
            <a href="#" className="btn-intro">More Dogs‘n Make Me Hot</a>
          </div>
        </section>
        <section className="hotdogs">
          {this.state.menuItems.map(el => 
            <div className="box-wrapper">
              <div className="left-box">
                <h2>{el.name}</h2>
                <p>
                  {el.description}
                </p>
              </div>
              <div className="right-box">
                <img className="img" src={el.backgroundURL} alt="hotdog"></img>
              </div>
            </div>
          )}
        </section>
      </div>
    );
  }
}
